@import url(https://fonts.googleapis.com/css2?family=Cuprum&display=swap);
body {
    margin: 0;
    width: 100%;
    /* background: linear-gradient(360deg, #cfd7c7 40%, #0b2027 120%); */
    background: #0b2027;
    font-family: 'Cuprum', sans-serif;
    /* scroll-behavior: smooth; */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}


/* HEADER */
header {
    top: 0;
}

.particles {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: -1;
  }

/* COVER */
header {
    background-color: transparent; 
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    background-clip: padding-box;
}

header h1, h2 {
    text-align: center;
    color: #cfd7c7;
}

.header-headline {
    display: flex;
    align-content: center;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    margin-top: 50px;
}

.header-cover {
    display: flex;
    justify-content: center;
    height: 30vh;
    margin-top: 0px;
}

.header-cover img {
    width: 210px;
    height: 200px;
    margin: auto 0;
    padding: 0px;
    border-radius: 200px;
    border: 2.5px solid #0b2027;
    image-orientation: 90deg;
}

@media only screen and (min-device-width: 320px) and (max-device-width: 600px) {
    img {
        width: 105;
        height: 95;
    }
}

#hr-style {
    width: 20vw;
    margin-bottom: 25px;
    border: 0;
    height: 0; /* Firefox... */
    box-shadow: 0 0 10px 1px black;
}

.buffer {
    height: 35px;
}

.header-nav {
    background-color: #cfd7cf;
}

/* MAIN NAV */
.sticky {
    position: fixed;
    top: 0;
    width: 100%;
}

.main-nav {
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin: 0;
    list-style: none;
    font-size: 1em;
    padding: 5px;
    cursor: pointer;
}

@media only screen and (min-device-width: 320px) and (max-width: 600px) {
    .main-nav {
        font-size: 0.7em;
        padding: 1px;
    }
}

.main-nav li {
    padding: 20px;
}

.main-nav li > li {
    margin-left: 0px;
}

.main-nav a {
    color: #0b2027;
    text-decoration: none;
    text-transform: capitalize;
    letter-spacing: 1px;
    font-size: 1.2em;
}

.active-link {
    border-bottom: 3px solid #a44a3f;
}

.main-nav .active {
    color: #a44a3f;
}

.logo {
    margin-right: 100px;
}
/* ABOUT */
.about {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
    grid-gap: 10px;
    gap: 10px;
    margin-top: 20px;
    color: #cfd7cf;
}

.about h1 {
    font-size: 50px;
    text-align: center;
    grid-row: 1fr;
    padding: 10px;
}

.about p {
    text-align: start;
    line-height: 1.5em;
    padding: 5px;
    font-size: 20px;
    font-style: normal;
    grid-row: span 1;
}

.block-text {
    margin-top: 0;
    margin-left: auto;
    margin-right: auto;
    width: 22em;
}

@media only screen and (min-width: 320px) and (max-width: 350px) {
    .about {
        width: auto;
        padding: 10px;
        display: grid;
        grid-template-rows: 1fr;
        grid-gap: 10px;
        gap: 10px;
        margin-top: 20px;
    }

    .block-text {
        margin-left: auto;
        margin-right: auto;
        width: 12em;
    }
}

@media only screen and (min-width: 350px) and (max-width: 480px) {
    .about {
        width: auto;
        padding: 10px;
        display: grid;
        grid-template-rows: 1fr;
        grid-gap: 10px;
        gap: 10px;
        margin-top: 20px;
    }

    .block-text {
        margin-left: auto;
        margin-right: auto;
        width: 15em;
    }
}

.view-resume {
    background-color: inherit;
    color: #a44a3f;
    padding: 20px;
    width: 12rem;
    margin: 50px 0;
    font-family: inherit;
    font-size: 20px;
    border: 3px solid #a44a3f;
    border-radius: 200px;
    cursor: pointer;
}

.projects {
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 20px;
    gap: 20px;
    justify-content: center;
}

.projects h1 {
    font-size: 50px;
    text-align: center;
    grid-row: 1fr;
    padding: 10px;
    color: #cfd7cf;
}

.box {
    padding: 10px;
    margin: 10px auto;
    color: #cfd7cf;
    display: flex;
    flex-direction: column;
    /* background:
    linear-gradient(27deg, #151515 5px, transparent 5px) 0 5px,
    linear-gradient(207deg, #151515 5px, transparent 5px) 10px 0px,
    linear-gradient(27deg, #222 5px, transparent 5px) 0px 10px,
    linear-gradient(207deg, #222 5px, transparent 5px) 10px 5px,
    linear-gradient(90deg, #1b1b1b 10px, transparent 10px),
    linear-gradient(#1d1d1d 25%, #1a1a1a 25%, #1a1a1a 50%, transparent 50%, transparent 75%, #242424 75%, #242424); */
    background-color: transparent;
    background-size: 20px 20px;
    box-shadow: #a44a3f;
    box-shadow: 1px 1px 5px 5px #cfd7cf;
}

.box:hover {
    box-shadow: 1px 1px 5px 6px #cfd7cf;
}

.box h3 {
    text-align: center;
}

.icons a {
    padding: 1px;
    color: #a44a3f;
    background-color: transparent;
    font-size: 1.5em;
}

.block-text {
    min-width: 20rem;
    max-width:  25rem;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    line-height: 1.5em;
    text-align: start;
    padding: 10px;
}

@media only screen and (min-width: 370px) and (max-width: 480px) {
    .box {
        width: auto;
    }

    .block-text {
        font-size: 1em;
        width: 12rem;
    }
}

@media only screen and (max-width: 370px) {
    .box {
        width: 80vw;
    }

    .block-text {
        font-size: 0.8em;
        min-width: 16rem;
    }
}

.center-horizontal {
    display: flex;
    justify-content: center;
}

.icons {
    display: flex;
    list-style: none;
    justify-content: space-around;
    padding: 10px;
    margin-top: auto;
    margin-bottom: 0;
}

footer {
    height: auto;
    background-color: whitesmoke;
    background-size: cover;
    margin-top: 50px;
    padding: 50px
}

footer > p {
    text-align: center;
}

footer a {
    color: inherit;
    text-decoration: underline;
}

/* SIDE NAV */
.body-nav {
    display: flex;
    justify-content: center;
}

.side-nav {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: auto;
    min-width: 20em;
    display: flex;
    list-style: none;
    margin: 5px;
    padding: 10px;
    justify-content: space-around;
    background-color: inherit;
    margin-top: 20px;
}

.side-nav li {
    font-size: 1.2em;
    padding: 10px;
}

.side-nav li:hover {
    transform: scale(1.1);
}

.side-nav a {
    margin: auto 0;
    padding: 10px;
    color: #0b2027;
    font-size: 1.5em;
}

@media only screen and (min-width: 320px) and (max-width: 600px) {
    .side-nav {
        width: auto;
        min-width: -webkit-fit-content;
        min-width: -moz-fit-content;
        min-width: fit-content;
        height: auto;
        padding: 5px;
    }

    .side-nav > li {
        font-size: 1em;
        margin: 5px;
        padding: 5px;
    }
}
